import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useWeb3 } from '../contexts/Web3Context';
import { useAuth } from '../contexts/AuthContext';
import toast from 'react-hot-toast';
import { Wallet, LogIn, ArrowRight } from 'lucide-react';

const LoginPage = () => {
    const navigate = useNavigate();
    const { account, isConnected, connect } = useWeb3();
    const { handleWalletLogin, isAuthenticated, isLoading } = useAuth();
    const [isAuthenticating, setIsAuthenticating] = useState(false);

    // Redirect if already authenticated
    useEffect(() => {
        if (isAuthenticated) {
            navigate('/');
        }
    }, [isAuthenticated, navigate]);

    const handleLogin = async () => {
        if (!isConnected) {
            toast.error('Please connect your wallet first');
            return;
        }

        try {
            setIsAuthenticating(true);
            await handleWalletLogin();
            // handleWalletLogin will redirect or show registration modal
        } catch (error) {
            console.error('Login error:', error);
            toast.error(error.message || 'Login failed');
        } finally {
            setIsAuthenticating(false);
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-primary-100 flex items-center justify-center px-4">
            <div className="max-w-md w-full">
                {/* Logo/Brand */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center justify-center w-16 h-16 bg-primary-600 rounded-full mb-4">
                        <Wallet className="w-8 h-8 text-white" />
                    </div>
                    <h1 className="text-3xl font-bold text-gray-900 mb-2">
                        NFT Ticketing System
                    </h1>
                    <p className="text-gray-600">
                        Secure, blockchain-based event ticketing
                    </p>
                </div>

                {/* Login Card */}
                <div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
                    <h2 className="text-2xl font-bold text-gray-900 mb-6 text-center">
                        Welcome Back
                    </h2>

                    <div className="space-y-4">
                        {/* Connect Wallet Button */}
                        {!isConnected ? (
                            <button
                                onClick={connect}
                                disabled={isLoading}
                                className="w-full flex items-center justify-center gap-3 px-6 py-4 bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-xl font-semibold hover:from-primary-700 hover:to-primary-800 transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                <Wallet className="w-5 h-5" />
                                Connect MetaMask Wallet
                            </button>
                        ) : (
                            <div className="space-y-4">
                                {/* Wallet Connected */}
                                <div className="bg-green-50 border border-green-200 rounded-xl p-4">
                                    <div className="flex items-center gap-2 text-green-700 mb-2">
                                        <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                                        <span className="font-semibold">Wallet Connected</span>
                                    </div>
                                    <p className="text-sm text-green-600 font-mono truncate">
                                        {account}
                                    </p>
                                </div>

                                {/* Login Button */}
                                <button
                                    onClick={handleLogin}
                                    disabled={isAuthenticating || isLoading}
                                    className="w-full flex items-center justify-center gap-3 px-6 py-4 bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-xl font-semibold hover:from-primary-700 hover:to-primary-800 transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50 disabled:cursor-not-allowed"
                                >
                                    {isAuthenticating ? (
                                        <>
                                            <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                                            Authenticating...
                                        </>
                                    ) : (
                                        <>
                                            <LogIn className="w-5 h-5" />
                                            Sign In with Wallet
                                            <ArrowRight className="w-5 h-5" />
                                        </>
                                    )}
                                </button>
                            </div>
                        )}
                    </div>

                    {/* Divider */}
                    <div className="relative my-8">
                        <div className="absolute inset-0 flex items-center">
                            <div className="w-full border-t border-gray-200"></div>
                        </div>
                        <div className="relative flex justify-center text-sm">
                            <span className="px-4 bg-white text-gray-500">
                                New to the platform?
                            </span>
                        </div>
                    </div>

                    {/* Sign Up Link */}
                    <Link
                        to="/signup"
                        className="block w-full text-center px-6 py-3 border-2 border-primary-600 text-primary-600 rounded-xl font-semibold hover:bg-primary-50 transition-all duration-200"
                    >
                        Create an Account
                    </Link>
                </div>

                {/* Info */}
                <div className="mt-6 text-center text-sm text-gray-600">
                    <p>
                        By connecting your wallet, you agree to our{' '}
                        <a href="#" className="text-primary-600 hover:underline">
                            Terms of Service
                        </a>{' '}
                        and{' '}
                        <a href="#" className="text-primary-600 hover:underline">
                            Privacy Policy
                        </a>
                    </p>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;
