import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useWeb3 } from '../contexts/Web3Context';
import { useAuth } from '../contexts/AuthContext';
import toast from 'react-hot-toast';
import { Wallet, UserPlus, Mail, Phone, User, ArrowRight, CheckCircle } from 'lucide-react';

const SignupPage = () => {
    const navigate = useNavigate();
    const { account, isConnected, connect } = useWeb3();
    const { register, isAuthenticated, isLoading, pendingAuth } = useAuth();

    const [formData, setFormData] = useState({
        username: '',
        email: '',
        phone: '',
        isOrganizer: false
    });

    const [errors, setErrors] = useState({});
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Redirect if already authenticated
    useEffect(() => {
        if (isAuthenticated) {
            navigate('/');
        }
    }, [isAuthenticated, navigate]);

    const validateForm = () => {
        const newErrors = {};

        // Username validation
        if (!formData.username.trim()) {
            newErrors.username = 'Username is required';
        } else if (formData.username.length < 3) {
            newErrors.username = 'Username must be at least 3 characters';
        }

        // Email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!formData.email.trim()) {
            newErrors.email = 'Email is required';
        } else if (!emailRegex.test(formData.email)) {
            newErrors.email = 'Invalid email format';
        }

        // Phone validation
        const phoneRegex = /^[0-9]{10,15}$/;
        if (!formData.phone.trim()) {
            newErrors.phone = 'Phone number is required';
        } else if (!phoneRegex.test(formData.phone.replace(/[\s\-\(\)]/g, ''))) {
            newErrors.phone = 'Invalid phone number (10-15 digits)';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
        // Clear error when user starts typing
        if (errors[name]) {
            setErrors(prev => ({ ...prev, [name]: '' }));
        }
    };

    const handleRoleChange = (isOrganizer) => {
        setFormData(prev => ({ ...prev, isOrganizer }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!isConnected) {
            toast.error('Please connect your wallet first');
            return;
        }

        if (!validateForm()) {
            toast.error('Please fix the form errors');
            return;
        }

        try {
            setIsSubmitting(true);
            await register(formData);
            toast.success('Account created successfully!');
            navigate('/');
        } catch (error) {
            console.error('Registration error:', error);
            toast.error(error.message || 'Registration failed');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-primary-50 via-white to-primary-100 py-12 px-4">
            <div className="max-w-2xl mx-auto">
                {/* Header */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center justify-center w-16 h-16 bg-primary-600 rounded-full mb-4">
                        <UserPlus className="w-8 h-8 text-white" />
                    </div>
                    <h1 className="text-3xl font-bold text-gray-900 mb-2">
                        Create Your Account
                    </h1>
                    <p className="text-gray-600">
                        Join the future of event ticketing
                    </p>
                </div>

                {/* Signup Card */}
                <div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
                    {/* Step 1: Connect Wallet */}
                    <div className="mb-8">
                        <div className="flex items-center gap-3 mb-4">
                            <div className={`flex items-center justify-center w-8 h-8 rounded-full ${isConnected ? 'bg-green-500' : 'bg-gray-300'} text-white font-bold`}>
                                {isConnected ? <CheckCircle className="w-5 h-5" /> : '1'}
                            </div>
                            <h3 className="text-lg font-semibold text-gray-900">Connect Wallet</h3>
                        </div>

                        {!isConnected ? (
                            <button
                                onClick={connect}
                                disabled={isLoading}
                                className="w-full flex items-center justify-center gap-3 px-6 py-4 bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-xl font-semibold hover:from-primary-700 hover:to-primary-800 transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50"
                            >
                                <Wallet className="w-5 h-5" />
                                Connect MetaMask Wallet
                            </button>
                        ) : (
                            <div className="bg-green-50 border border-green-200 rounded-xl p-4">
                                <div className="flex items-center gap-2 text-green-700 mb-2">
                                    <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                                    <span className="font-semibold">Wallet Connected</span>
                                </div>
                                <p className="text-sm text-green-600 font-mono truncate">
                                    {account}
                                </p>
                            </div>
                        )}
                    </div>

                    {/* Step 2: Fill Form */}
                    {isConnected && (
                        <>
                            <div className="mb-8">
                                <div className="flex items-center gap-3 mb-6">
                                    <div className="flex items-center justify-center w-8 h-8 rounded-full bg-primary-600 text-white font-bold">
                                        2
                                    </div>
                                    <h3 className="text-lg font-semibold text-gray-900">Your Information</h3>
                                </div>

                                <form onSubmit={handleSubmit} className="space-y-5">
                                    {/* Username */}
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Username *
                                        </label>
                                        <div className="relative">
                                            <User className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                                            <input
                                                type="text"
                                                name="username"
                                                value={formData.username}
                                                onChange={handleChange}
                                                className={`w-full pl-11 pr-4 py-3 border ${errors.username ? 'border-red-500' : 'border-gray-300'} rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all`}
                                                placeholder="Enter your username"
                                            />
                                        </div>
                                        {errors.username && (
                                            <p className="mt-1 text-sm text-red-600">{errors.username}</p>
                                        )}
                                    </div>

                                    {/* Email */}
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Email Address *
                                        </label>
                                        <div className="relative">
                                            <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                                            <input
                                                type="email"
                                                name="email"
                                                value={formData.email}
                                                onChange={handleChange}
                                                className={`w-full pl-11 pr-4 py-3 border ${errors.email ? 'border-red-500' : 'border-gray-300'} rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all`}
                                                placeholder="your.email@example.com"
                                            />
                                        </div>
                                        {errors.email && (
                                            <p className="mt-1 text-sm text-red-600">{errors.email}</p>
                                        )}
                                    </div>

                                    {/* Phone */}
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Phone Number *
                                        </label>
                                        <div className="relative">
                                            <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                                            <input
                                                type="tel"
                                                name="phone"
                                                value={formData.phone}
                                                onChange={handleChange}
                                                className={`w-full pl-11 pr-4 py-3 border ${errors.phone ? 'border-red-500' : 'border-gray-300'} rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all`}
                                                placeholder="+1 (555) 123-4567"
                                            />
                                        </div>
                                        {errors.phone && (
                                            <p className="mt-1 text-sm text-red-600">{errors.phone}</p>
                                        )}
                                    </div>

                                    {/* Role Selection */}
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-3">
                                            Account Type *
                                        </label>
                                        <div className="grid grid-cols-2 gap-4">
                                            {/* Customer */}
                                            <button
                                                type="button"
                                                onClick={() => handleRoleChange(false)}
                                                className={`p-4 border-2 rounded-xl transition-all ${!formData.isOrganizer
                                                        ? 'border-primary-600 bg-primary-50'
                                                        : 'border-gray-200 hover:border-gray-300'
                                                    }`}
                                            >
                                                <div className="text-center">
                                                    <div className="text-2xl mb-2"></div>
                                                    <div className="font-semibold text-gray-900">Customer</div>
                                                    <div className="text-xs text-gray-600 mt-1">
                                                        Buy and manage tickets
                                                    </div>
                                                </div>
                                            </button>

                                            {/* Organizer */}
                                            <button
                                                type="button"
                                                onClick={() => handleRoleChange(true)}
                                                className={`p-4 border-2 rounded-xl transition-all ${formData.isOrganizer
                                                        ? 'border-primary-600 bg-primary-50'
                                                        : 'border-gray-200 hover:border-gray-300'
                                                    }`}
                                            >
                                                <div className="text-center">
                                                    <div className="text-2xl mb-2"></div>
                                                    <div className="font-semibold text-gray-900">Organizer</div>
                                                    <div className="text-xs text-gray-600 mt-1">
                                                        Create and manage events
                                                    </div>
                                                </div>
                                            </button>
                                        </div>
                                    </div>

                                    {/* Submit Button */}
                                    <button
                                        type="submit"
                                        disabled={isSubmitting || isLoading}
                                        className="w-full flex items-center justify-center gap-3 px-6 py-4 bg-gradient-to-r from-primary-600 to-primary-700 text-white rounded-xl font-semibold hover:from-primary-700 hover:to-primary-800 transition-all duration-200 shadow-lg hover:shadow-xl disabled:opacity-50 disabled:cursor-not-allowed mt-6"
                                    >
                                        {isSubmitting ? (
                                            <>
                                                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                                                Creating Account...
                                            </>
                                        ) : (
                                            <>
                                                <UserPlus className="w-5 h-5" />
                                                Create Account
                                                <ArrowRight className="w-5 h-5" />
                                            </>
                                        )}
                                    </button>
                                </form>
                            </div>
                        </>
                    )}

                    {/* Login Link */}
                    <div className="mt-6 text-center text-sm text-gray-600">
                        Already have an account?{' '}
                        <Link to="/login" className="text-primary-600 font-semibold hover:underline">
                            Sign In
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SignupPage;
